

@media only screen and (min-width: 1024px) {.header {
    background-color: var(--primary-color-1);
    position: sticky;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: center;
}


.navbar {
    padding: 0 !important;
    margin: auto 0 !important;
    width: 80vw;
    display: flex;
    gap: 20px
}
.navbar-brand {
    margin-right: 0 !important;
}
.header-logo {
    max-height: 100px;
    max-width: 100px;
   
}


.header-cart-item-img {
    height: 100%;
    width: 100%;
}
 .navbar-collapse {
    width: 200px !important
 }
.navbar-nav {
    display: flex;
    justify-content: center !important;;
    background-color: #0b603d;
    border-radius: 10%;
    padding: 0 auto;
    width: 100%
}


.nav-link {
    color: white !important;
    font-size: 1.1rem !important;
}


.nav-link:hover {
    border: none !important;
}


/*Cart */
.search-and-cart {
    display: flex;
    gap: 1.3rem
}


/*Cart button*/
.cart-container {
    border: 2px solid var(--primary-color-1)
}


.cart-btn {
    background-color: white !important;
    color: var(--primary-color-1) !important;
    font-size: 0.8rem !important;
}


.cart-quantity {
    display: inline-block;
    background-color: var(--primary-color-2);
    color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 0.8rem;
    display: hidden !important;
}


.fa-shopping-cart {
    color: var(--primary-color-1);
    font-size: 1rem;
}


.cart-container {
    position: absolute;
    top: 100px;
    right: 0px;
    max-width: 600px;
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
    border-radius: 5px;
    color: #0b603d;
}


.cart-item {
    background-color: white;
    opacity: 1;
    padding: 10px;
    text-align: center;
    align-items: center;
    border-bottom: #c8c8c8 solid 2px;
    font-size: 16px;
    font-weight: 600;
}


.cart-name {
    font-size: 1.3rem;
    text-align: left;
}


.price-item {
    font-size: 1.3rem !important;
}


.close {
    border: none;
    background: none;
    color: #0b603d;
    transition: all 0.3s;
   
}


.close:hover {
    transform: scale(1.1);
    z-index: 1;
}


.content-cart {
    background-color: white;
    opacity: 1;
    display: flex;
    justify-content: space-around;
    padding: 10px;
    text-align: center;
    align-items: center;
    border-bottom: #c8c8c8 solid 2px;
    font-size: 16px;
    font-weight: 600;
    color: var(--background-color);
    font-size: 1.2rem;
    background-color: var(--background-color-1);
}


.total-price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.3rem;
}


.total-price h4 {
    color: black;
}


.total-price span {
    color: var(--primary-color-2);
    font-weight: 700;
}


.btn-checkout {
    background-color: #0b603d;
    color: #fff;
    text-align: center;
    width: 100%;
    border: none;
    padding: 10px 0px;
    transition: all 0.3s;
    opacity: 0.9;
}


.btn-checkout:hover {
    opacity: 1;
    background-color: var(--primary-color-2);
}


/* search */




.search-container {
    position: relative;
    max-width: 170px
  }


  .search-container input {
    width: 100%
  }
 
  .search-input {
    border-radius: 15px;
    width: 300px;
    height: 40px;
    padding: 5px;
    border: 1px solid #ccc;
   
  }


  .search-icon {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }
 
 
  .search-icon svg {
    width: 16px;
    height: 26px;
    color: #221f1f;
  }
 
  .search-results {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    max-height: 200px;
    overflow-y: auto;
    background-color: #fff;
    border: 1px solid #ccc;
    border-top: none;
    border-radius: 0 0 5px 5px;
    z-index: 1;
  }
 
  .search-results-category {
    padding: 10px;
  }
 
  .search-result-item {
    display: block;
    padding: 5px;
    color: #333;
    text-decoration: none;
  }
 
  .search-result-item:hover {
    background-color: #f5f5f5;
  }
}


